import React from 'react';
import { useWallet } from "@solana/wallet-adapter-react";

import '../css/Header.scss';
import '../css/Flex.scss';

import ProfileImg from '../assets/profile_img.svg';

export default function Header() {
    const openProfile = () => {
        console.log('open profile');
    }
    const openWallet = () => {
        console.log('open wallet');
    }
    const goHome = () => {
        console.log('go home');
    }

    const wallet = useWallet();
    let wallet_status = '';
    if (wallet.connected) {
        wallet_status =
            <div className='wallet-status-container flex-container space-around' onClick={openWallet}>
                <div className='wallet-status wallet-status-connected'></div>
                <h3 className='status'>Wallet Connected</h3>
            </div>
    } else {
        wallet_status =
            <div className='wallet-status-container flex-container space-around' onClick={openWallet}>
                <div className='wallet-status wallet-status-disconnected'></div>
                <h3 className='status'>Wallet Disconnected</h3>
            </div >
    }
    return (
        <header className='header flex-container space-between'>
            <h2 className='logo' onClick={goHome}>Aces</h2>
            <h2 className='title'>{global.config.homeTitle}</h2>
            <div className='header-right-container flex-container space-around'>
                {wallet_status}
                <img className='profile' src={ProfileImg} onClick={openProfile} alt="white circle with a person's shape cutout"></img>
            </div>
        </header>
    )
}
