export default function WalletOverview() {

  // const connection = new Connection("https://api.mainnet-beta.solana.com");
  const connection = new Connection("https://solana-api.projectserum.com");
  const wallet = useWallet();
  const [pacesAmount, setPacesAmount] = useState(global.config.pacesInWallet);

  useEffect(() => {
    (async () => {
      if (wallet.connected && global.config.nftData.metadata.length === 0) {

        console.log("wallet connected");
        // =====      START to fetch all NFT Metadata in wallet      ===== //
        const nftsmetadata = await metadata.Metadata.findDataByOwner(connection,
          new PublicKey(wallet.publicKey), // owner here
          {
            programId: TOKEN_PROGRAM_ID,
          });
        global.config.nftData.metadata = nftsmetadata;

        var cards = 0;
        global.config.nftData.cardUris = [];
        nftsmetadata.forEach((e) => {
          if (e.data.symbol === 'ACES') {
            cards++;
            global.config.nftData.cardUris.push(e.data.uri);
          }
        });
        console.log("nftsmetadata : ", nftsmetadata);
        global.config.nftData.cards = cards;
        console.log("global.config.nftData.cards : ", global.config.nftData.cards);

        // =====      END to fetch all NFT Metadata in wallet      ===== //



        // =====      START TO GET PACES AMOUNT IN WALLET         ===== //

        const pacesTokenAddress = global.config.pacesTokenAddress;

        let response = await connection.getTokenAccountsByOwner(
          new PublicKey(wallet.publicKey.toBase58()), // owner here
          {
            programId: TOKEN_PROGRAM_ID,
          }
        );

        response.value.forEach((e) => {
          const accountInfo = SPLToken.AccountLayout.decode(e.account.data);
          const amount = parseInt(SPLToken.u64.fromBuffer(accountInfo.amount));

          const splTokenAddress = new PublicKey(accountInfo.mint).toString()
          console.log("accountInfo : ", accountInfo);
          console.log("amount : ", amount);
          if (splTokenAddress === pacesTokenAddress) {

            // const pubkey = e.pubkey.toBase58();
            // let pacesTokenAccount = "";
            // pacesTokenAccount = pubkey;
            // const pacesTokenBalance = connection.getTokenAccountBalance(new PublicKey(pacesTokenAccount));
            // console.log('pacesTokenBalance:', pacesTokenBalance);

            global.config.pacesInWallet = amount;
            setPacesAmount(amount);
          }
        });
        // =====      END TO GET PACES AMOUNT IN WALLET         ===== //
      }
    })();
  }, []);

  return (
    <div className='flex-container column-flex overview-container'>
      <h2 className='title'>Your Wallet</h2>
      <div className='flex-container icon-container'>
        <h1 className='total'>{pacesAmount}</h1>
        <img src={CoinImg} className='coin-img' alt='gold coin with a skull on it' />
        <p className='spaces'>$paces</p>
        <div className="row  d-flex justify-content-center">
        </div>
      </div>
    </div>
  )
}