import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useLocation } from 'react-router-dom';

import { useWallet } from "@solana/wallet-adapter-react";
import { Hand } from "pokersolver"

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import BlueButton from './buttons/BlueButton';
import CheckButton from './buttons/CheckButton';
import RaiseButton from './buttons/RaiseButton';

import GameType from './GameType';
import { GameStatus } from './Utils/GameStatus';
import { GameResult } from './Utils/GameResult';
import { CardType } from './Utils/CardType';

import HomeButton from './HomeButton';
import Card from './Card';
import Loading from './Loading';
import dealer_cards from './Utils/DealerCards'

import CoinImg from '../assets/GoldAcesChip.svg';
import DealerDotsCard from '../assets/dealerEmptyCard.svg';
import CardsSmall from '../assets/CardsSmall.svg';
import NFTDotsCard from '../assets/nftEmptyCard.svg';

import axios from 'axios';

import '../css/Table.scss';


export default function Table({ game }) {

    const navigate = useNavigate();
    const wallet = useWallet();

    if (!wallet.connected)
        navigate('/');


    const [pot, setPot] = useState(0);
    const [round, setRound] = useState(0);
    const [player_bet, setPlayerBet] = useState(100);
    const [total_earned, setTotalEarned] = useState(0);
    const [playable_cards, setPlayableCards] = useState(0);
    const [show_loading, setShowLoading] = useState(false);

    const [player_not_played, setPlayerNotPlayed] = useState([]);
    const [player_in_play, setPlayerInPlay] = useState([{ content: <div className={'your-in-play'}></div>, symbol: '' }]);
    const [player_discards, setPlayerDiscards] = useState([]);

    const [dealer_not_played, setDealerNotPlayed] = useState([]);
    const [dealer_in_play, setDealerInPlay] = useState([]);
    const [dealer_discards, setDealerDiscards] = useState([]);

    const [player_paces_total, setPlayerPacesTotal] = useState(global.config.pacesInWallet);
    const [gameType, setGameType] = useState('');
    const [game_status, setGameStatus] = useState(GameStatus.BET);
    const [game_result, setGameResult] = useState(GameResult.EMPTY);

    // Fetch ACE Cards URIs
    const [acesCards, setAcesCards] = useState([]);

    useEffect(() => {
        const FetchCardUris = async () => {
            global.config.nftData.cardUris.forEach(cardUri => {

                axios.get(cardUri).then(response => {
                    let attributes = response.data.attributes;
                    let card = '';
                    attributes.forEach(item => {
                        if (item.trait_type === 'Value') {
                            let itemValue = item.value.trim();
                            if (!isNaN(itemValue))
                                if (parseInt(itemValue) !== 10)
                                    card = itemValue + card;
                                else
                                    card = 'T' + card;
                            else
                                card = itemValue.charAt(0).toUpperCase() + card;
                        }
                        if (item.trait_type === 'Suit')
                            card = card + item.value.charAt(0);
                    });
                    setAcesCards(acesCardUris => [...acesCardUris, { 'symbol': card, 'image': response.data.image }]);
                }).catch(error => {
                    console.log("Error fetching Image URL : ", error);
                })
            });
        }

        console.log("global.config.nftData.acesCardUris : ", global.config.nftData.acesCardUris);

        if (global.config.nftData.acesCardUris.length === 0) {
            FetchCardUris();
            setShowLoading(true);
        }
        else {
            setAcesCards(global.config.nftData.acesCardUris);
        }
    }, []);


    useEffect(() => {
        // Check whether All of the NFT Card Uris were downloaded 
        if (acesCards.length === global.config.nftData.cards && global.config.nftData.acesCardUris.length === 0) {

            // Remove duplicated ACES cards with symbol
            let temp = removeDuplicatedCards(acesCards);
            global.config.nftData.acesCardUris = temp;
            setShowLoading(false);
            console.log("Temp : ", temp);

            setAcesCards(temp);
        }


        if (acesCards.length > global.config.maxCardsToPlay)
            setPlayableCards(global.config.maxCardsToPlay);
        else
            setPlayableCards(acesCards.length);
    }, [acesCards]);


    useEffect(() => {
        initGameRoom();
    }, [playable_cards, gameType]);


    const location = useLocation();
    if (location.state) {
        game = location.state.game;
    }

    let deal = 1;
    if (game === 'war') {
        deal = 1;
    } else if (game === '3 card') {
        deal = 3;
    } else if (game === '5 card') {
        deal = 5;
    }

    // Check User entered to another Game Room
    if (game !== gameType) {
        setGameType(game)
    }



    // =====    BEGIN Player and Dealer In-Play Area    ===== //

    const cardsInPlayDealer = [];
    const cardsInPlayPlayer = [];

    const mid = parseInt(deal / 2);

    for (var i = 0; i < deal; i++) {

        cardsInPlayDealer[i] =
            <div className='dealer-card' key={'dealer-card-' + i}>
                {
                    dealer_in_play[i] === undefined
                        ? <div className='dealer-card-pile dealer-in-play' key={i}></div>
                        : <img className='dealer-card-image-pile' src={dealer_in_play[i].image} alt="card full of dots" />
                }
            </div>

        if (i === mid) {
            cardsInPlayPlayer.push(
                <div className={'your-card your-card-' + i} key={'your-card your-card-' + i}>
                    <p className="your-card-title">In Play</p>
                    <div className="your-card-pile">
                        {player_in_play[i + 1] !== undefined ? player_in_play[i + 1].content : player_in_play[0].content}
                    </div>
                </div>
            );
        } else {
            cardsInPlayPlayer.push(
                <div className={'your-card your-card-' + i} key={'your-card your-card-' + i}>
                    <p className="your-card-title"></p>
                    <div className="your-card-pile">
                        {player_in_play[i + 1] !== undefined ? player_in_play[i + 1].content : player_in_play[0].content}
                    </div>
                </div>
            );
        }
    }

    // =====    END Player and Dealer In-Play Area    ===== //



    // =====    BEGIN Player Game Result Area    ===== //
    let game_result_container = '';

    if (game_status === GameStatus.ENDED) {

        if (game_result === GameResult.WIN)
            game_result_container =
                <div className="game-result-notification game-result-notification-success">
                    You Win!
                </div>
        else if (game_result === GameResult.LOST)
            game_result_container =
                <div className="game-result-notification game-result-notification-lost">
                    You Lost!
                </div>

    }
    // =====    End Player Game Result Area    ===== //



    // =====    BEGIN to check who wins         ===== //

    useEffect(() => {
        if (game_status === GameStatus.ENDED) {
            // Decide Winner
            var player_symbols = [];
            player_in_play.forEach(item => {
                if (item.symbol !== '')
                    player_symbols.push(item.symbol);
            })

            console.log("player_in_play: ", player_in_play);
            console.log("dealer_in_play : ", dealer_in_play);

            var dealer_symbols = [];
            dealer_in_play.forEach(item => {
                dealer_symbols.push(item.symbol);
            })

            console.log("Player Symbolx : ", player_symbols);
            console.log("Dealer Symbols : ", dealer_symbols);


            // Check whether player has Joker Wild Card or not.

            if (player_symbols.indexOf('Jj') === -1) {
                console.log('Not JJ');
                decideWinner(player_symbols, dealer_symbols);
            } else {
                console.log('JJ');
                for (var player_index = 0; player_index < player_in_play.length; player_index++) {
                    if (player_symbols[player_index] === 'Jj') {
                        for (var i = 1; i <= 13; i++) {
                            var number = i;
                            if (number === 1)
                                number = 'A';
                            else if (number === 10)
                                number = 'T';
                            else if (number === 11)
                                number = 'J';
                            else if (number === 12)
                                number = 'Q';
                            else if (number === 13)
                                number = 'K';

                            var temp_player_symbols = [...player_symbols];
                            var tempSymbol = number + 'c';
                            temp_player_symbols[player_index] = tempSymbol;

                            if (decideWinner(temp_player_symbols, dealer_symbols))
                                break;

                            tempSymbol = number + 'd';
                            temp_player_symbols[player_index] = tempSymbol;
                            if (decideWinner(temp_player_symbols, dealer_symbols))
                                break;

                            tempSymbol = number + 'h';
                            temp_player_symbols[player_index] = tempSymbol;
                            if (decideWinner(temp_player_symbols, dealer_symbols))
                                break;

                            tempSymbol = number + 's';
                            temp_player_symbols[player_index] = tempSymbol;
                            if (decideWinner(temp_player_symbols, dealer_symbols))
                                break;

                        }
                    }
                }
            }
        }
    }, [game_status])


    const decideWinner = (player_symbols, dealer_symbols) => {
        var temp_player_symbols = removeDuplicatedSymbols(player_symbols);
        console.log("temp_player_symbols : ", temp_player_symbols);
        console.log("player_symbols : ", player_symbols);
        if (temp_player_symbols.length !== player_symbols.length)
            return false;

        var player = Hand.solve(player_symbols);
        var dealer = Hand.solve(dealer_symbols);
        var winner = Hand.winners([player, dealer]);

        console.log("player_symbols : ", player_symbols);

        setTimeout(function () {
            if (winner[0] === player) {
                // Increase Paces
                setGameResult(GameResult.WIN);
                setTotalEarned(total_earned + pot);
                return true;
            }
            else {
                // Decrease Paces
                setGameResult(GameResult.LOST);
                setTotalEarned(total_earned - pot);
                return false;
            }
        }, global.config.delayPokerSolver);
    }

    // =====    END     to check who wins       ===== //


    // =====    BEGIN Define Functions    ===== //

    const shuffle = (array) => {
        for (var i = array.length - 1; i > 0; i--) {
            var j = Math.floor(Math.random() * (i + 1));
            var temp = array[i];
            array[i] = array[j];
            array[j] = temp;
        }
    };

    // Initialize Game Room
    const initGameRoom = () => {

        // Shuffle Card URI
        shuffle(acesCards);
        shuffle(dealer_cards);

        setDealerNotPlayed(dealer_cards.slice(0, playable_cards));

        let temp_cards_array = [];
        acesCards.some(function (card, index) {
            // const divPlay = <Card frontImage={card.image} backImage={NFTDotsCard} />
            temp_cards_array.push({ content: card.image, symbol: card.symbol });
            return index + 1 === playable_cards;
        });

        console.log("temp_cards_array : ", temp_cards_array);
        setPlayerNotPlayed(temp_cards_array);

        setPlayerInPlay([{ content: <div className={'your-in-play'}></div>, symbol: '' }]);
        setPlayerDiscards([]);
        setDealerInPlay([]);
        setDealerDiscards([]);
    }

    function removeDuplicatedCards(array) {
        const symbols = array.map(o => o.symbol);
        let filtered = array.filter(({ symbol }, index) => !symbols.includes(symbol, index + 1));
        return filtered;
    }

    function removeDuplicatedSymbols(array) {
        let uniqueChars = array.filter((symbol, index) => {
            return array.indexOf(symbol) === index;
        });
        return uniqueChars;
    }


    const subtractBet = () => {
        if ((player_bet - 100) > 99) {
            setPlayerBet(player_bet - 100);
        } else {
            setPlayerBet(100);
        }
    }

    const addBet = () => {
        setPlayerBet(player_bet + 100);
    }

    const placeBet = () => {
        if (player_not_played.length < deal) {
            toast.error('You don\'t have enough NFT card!', {
                position: "top-center",
                autoClose: 5000,
                className: "error-toast-container",
                toastClassName: "error-toast",
                bodyClassName: "error-toast-body",
                progressClassName: "error-toast-progress",
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            return;
        }


        if (player_paces_total >= player_bet && player_paces_total !== 0) {
            setPot(pot + player_bet * 2);
            setPlayerPacesTotal(player_paces_total - player_bet);
            // setPlayerBet(100);
            setRound(round + 1);
            setGameStatus(GameStatus.PLAYING);

            openYourCard();

            // Open the first card in Your Cards
        } else {
            toast.error('You can not make this bet!', {
                position: "top-center",
                autoClose: 5000,
                className: "error-toast-container",
                toastClassName: "error-toast",
                bodyClassName: "error-toast-body",
                progressClassName: "error-toast-progress",
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    }

    const playAgain = () => {
        setRound(0);
        setPot(0);
        setPlayerBet(100);
        setGameStatus(GameStatus.BET);
        setGameResult(GameResult.EMPTY);


        // Move all cards from In-Play to Discards
        setPlayerDiscards(player_discards => [...player_discards, ...player_in_play.slice(1)]);
        setPlayerInPlay([{ content: <div className={'your-in-play'}></div>, symbol: '' }]);

        setDealerDiscards(dealer_discards => [...dealer_discards, ...dealer_in_play]);
        setDealerInPlay([]);

        // Move all cards from Discards to Not-Played
        if (deal > player_not_played.length) {
            // setPlayerNotPlayed(player_not_played => [...player_not_played, ...player_discards]);
            // setDealerNotPlayed(dealer_not_played => [...dealer_not_played, ...dealer_discards]);

            initGameRoom();
        }
    }

    const checkBet = () => {
        //setRound(round + 1);
        openYourCard();
    }

    const raiseBet = () => {
        setPot(pot + player_bet * 2);
        setPlayerPacesTotal(player_paces_total - player_bet);
        setPlayerBet(100);
        // setRound(round + 1);
    }

    const openYourCard = () => {
        if (player_not_played.length !== 0) {

            // Player Card Management
            const topPlayCard = player_not_played[player_not_played.length - 1];
            console.log("topPlayCard : ", topPlayCard);

            const divImg = <img className='your-real-card-pile' src={topPlayCard.content} alt="card full of dots" />

            setPlayerNotPlayed(player_not_played.slice(0, -1));
            setPlayerInPlay(player_in_play => [...player_in_play, { content: divImg, symbol: topPlayCard.symbol }]);

            const topDealerCard = dealer_not_played.pop();
            setDealerNotPlayed(dealer_not_played);
            setDealerInPlay(oldValue => [...oldValue, topDealerCard]);
        }
    }


    // =====    BEGIN to check whether Round is ended    ===== //

    useEffect(() => {
        if (player_in_play.length === deal + 1 && dealer_in_play.length === deal)
            setGameStatus(GameStatus.ENDED);
    }, [player_in_play, dealer_in_play])

    // =====    END to check whether Round is ended      ===== //


    return (
        <>
            <div className='game-header flex-container space-between'>
                <HomeButton type="return" content="Return Home" />
                {/* <GameType game={game} onChangeGame={this.changeGameType}/> */}
                <GameType game={game} />
            </div>
            <div className='table-container'>
                <div className={'table table-' + deal}>
                    <div className="dealer-side">
                        DEALER
                    </div>
                    <div className='dealer-cards'>
                        <div className='dealer-card'>
                            <div className='dealer-card-pile'>
                                <img className='dealer-real-card-pile' src={DealerDotsCard} alt="card full of dots" />
                            </div>
                        </div>
                        {cardsInPlayDealer}
                        <div className='dealer-card dealer-last-card'>
                            <div className='dealer-card-pile'>
                                <img className='dealer-real-card-pile dealer-card-rotate' src={DealerDotsCard} alt="card full of dots" />
                            </div>
                        </div>
                    </div>
                    <div className='table-middle-section'>
                        <hr className='boundary-line'></hr>
                        <div className='pot-section'>
                            <div className='pot-title'>Pot : {pot}</div>
                            <img src={CoinImg} className='pot-coin' alt="gold coin with a skull on it" />
                        </div>
                        <hr className='boundary-line'></hr>
                    </div>
                    {show_loading &&
                        <Loading></Loading>
                    }
                    <div className='your-cards'>
                        <div className='your-card your-first-card'>
                            <p className='your-card-title'>Your Cards ({player_not_played.length})</p>
                            <div className='your-card-pile'>
                                {/* {player_not_played} */}
                                <img className='your-real-card-pile your-card-rotate' src={NFTDotsCard} alt="card full of dots" />
                            </div>
                        </div>
                        {cardsInPlayPlayer}
                        <div className='your-card your-last-card'>
                            <p className='your-card-title'>Discard Pile ({player_discards.length})</p>
                            <div className='your-card-pile'>
                                {/* {player_discards} */}
                                <img className='your-real-card-pile' src={NFTDotsCard} alt="card full of dots" />
                            </div>
                        </div>
                    </div>
                    {game_result_container}
                    <div className="your-side">
                        YOU
                    </div>
                    {total_earned !== 0 &&
                        <div className="your-earned">
                            {(total_earned < 0 ? "" : "+") + total_earned}
                        </div>
                    }
                </div>
            </div>
            <div className='game-footer-container flex-container'>
                <div className='game-footer flex-container space-evenly'>
                    <div className='player-bet-container'>
                        <div className='flex-container column-flex'>
                            <div className='player-bet flex-container'>
                                <div className='minus-btn' onClick={subtractBet}>
                                    <p className='btn-icon flex-container'>-</p>
                                </div>
                                <div className='bet-container flex-container'>
                                    <h2 className='bet'>{player_bet}</h2>
                                    <img src={CoinImg} className="player-coin" alt="gold coin with a skull on it" />
                                </div>
                                <div className='plus-btn' onClick={addBet}>
                                    <p className='btn-icon flex-container'>+</p>
                                </div>
                            </div>
                            <p className='bet-label'>Bet Amount</p>
                        </div>
                    </div>
                    {game_status === GameStatus.ENDED &&
                        <BlueButton content="Play Again" type="playAgain" action={playAgain} />}
                    {game_status === GameStatus.BET && round === 0 &&
                        <BlueButton content="Bet" type="bet" action={placeBet} />}
                    {game_status === GameStatus.PLAYING && (game === '3 card' || game === '5 card') &&
                        <div className='in-game-action'>
                            <CheckButton action={checkBet} className='check-btn' />
                            <RaiseButton action={raiseBet} className='raise-btn' />
                        </div>}
                    {/* {((round === 1 && game === 'war') || (round === 2 && game === '3 card') || (round === 4 && game === '5 card')) &&
                        <BlueButton content="Play Again" action={playAgain} />
                    } */}
                    <div className='total-spaces-container'>
                        <div className='spaces-amt-container flex-container'>
                            <p className='spaces-amt'>{player_paces_total}</p>
                            <img className='spaces-img' src={CoinImg} alt="gold coin with a skull on it" />
                        </div>
                        <p className='total-spaces-label'>Total $PACES</p>
                    </div>
                </div>
            </div>
            <ToastContainer
                position="top-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />
        </>
    )
}