import React from 'react';

import { useWallet, WalletProvider, ConnectionProvider } from '@solana/wallet-adapter-react';
import {
    WalletModalProvider,
    WalletDisconnectButton,
    WalletMultiButton
} from "@solana/wallet-adapter-react-ui";


import Choice from './Choice';
import WalletConnected from './WalletConnected';
import WalletNotConnected from './WalletNotConnected';

import WarImg from '../assets/warCard.png';
import ThreeImg from '../assets/threeCard.png';
import FiveImg from '../assets/fiveCard.png';

import '../css/Home.scss';

export default function Home() {
    const wallet = useWallet();

    const warDesc = "1 card against 1 card. Doesn’t get any simpler than that!";
    const threeCardDesc = "3 card vs 3 card. Up the ante as you go.";
    const fiveCardDesc = "5 card vs 5 card. Up the ante as you go.";

    if (wallet.connected) {
        return (
            <div className='home flex-container column-flex'>
                <WalletConnected></WalletConnected>
                <WalletModalProvider>
                    {/* <WalletDisconnectButton /> */}
                </WalletModalProvider>
                <div className='choices-container flex-container space-between'>
                    <Choice title="War" description={warDesc} image={WarImg} />
                    <Choice title="3 Card" description={threeCardDesc} image={ThreeImg} />
                    <Choice title="5 Card" description={fiveCardDesc} image={FiveImg} />
                </div>
            </div>
        );
    }
    else {
        return (
            <div className='home flex-container column-flex'>
                <WalletNotConnected></WalletNotConnected>
                <div className='choices-container flex-container space-between'>
                    <Choice title="War" description={warDesc} image={WarImg} />
                    <Choice title="3 Card" description={threeCardDesc} image={ThreeImg} />
                    <Choice title="5 Card" description={fiveCardDesc} image={FiveImg} />
                </div>
            </div>
        );
    }
}