import React from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { useWallet } from "@solana/wallet-adapter-react";


import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { StyledEngineProvider } from '@mui/material';
import { borderRadius } from '@mui/system';


// For Dialog 
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';

import BlueButton from './buttons/BlueButton';
import ConnectBlueButton from './ConnectBlueButton';

import '../css/Choice.scss';


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up"
        ref={ref} {...props}
    />;
});

export default function Choice({ title, description, image }) {
    const navigate = useNavigate();
    const wallet = useWallet();

    const [openAlertDialog, setOpenAlertDialog] = React.useState(false);

    const handleClose = () => {
        setOpenAlertDialog(false);
    };


    // =====        START OF DIALOG CONTENT         ===== //

    let dialogContent = '';

    if (wallet.connected) {
        if (global.config.nftData.cards < global.config.minCardsToPlay) {
            dialogContent =
                <DialogContentText id="alert-dialog-slide-description">
                    You need 3 ACES NFTs to play this game.
                    Visit <a href={global.config.magicedenUrl} target="_blank" className='buy-token'>Magic Eden</a> to buy a few more.
                </DialogContentText>

        } else if (global.config.pacesInWallet < global.config.minPacesToPlay) {
            dialogContent =
                <DialogContentText id="alert-dialog-slide-description">
                    You need {global.config.minPacesToPlay} PEACES tOKEN to play this game.
                    Visit <a href={global.config.famousfoxesUrl} target="_blank" className='buy-token'>Famous Fox Federation</a> to buy a few more.
                </DialogContentText>
        }
    }


    // =====        END OF DIALOG CONTENT         ===== //


    const goToGame = () => {

        // There are more NFT cards in wallet than minimum cards to play.
        if (!wallet.connected
            || global.config.nftData.cards < global.config.minCardsToPlay
            || global.config.pacesInWallet < global.config.minPacesToPlay) {
            setOpenAlertDialog(true);
        } else
            navigate(`/table`, {
                state: {
                    game: title.toLowerCase()
                }
            });
    }


    return (
        <StyledEngineProvider injectFirst>
            <Card sx={{ maxWidth: 300 }} className="game-choice-card">
                <CardMedia
                    component="img"
                    height="170"
                    image={image}
                    alt=""
                    className='game-choice-img'
                />
                <CardContent className='game-choice-content'>
                    <Typography className="title" gutterBottom variant="h5" component="div">
                        {title}
                    </Typography>
                    <Typography gutterBottom className="game-description">
                        {description}
                    </Typography>
                    <Typography className='bet' gutterBottom variant="p" component="div">
                        100 minimum bet
                    </Typography>
                    <CardActions>
                        {!wallet.connected && <ConnectBlueButton content='Connect Wallet' />}
                        {wallet.connected && <BlueButton content="Play Now" type="game-choice" action={goToGame} />}
                    </CardActions>
                </CardContent>

                <Dialog
                    open={openAlertDialog}
                    TransitionComponent={Transition}
                    keepMounted
                    onClose={handleClose}
                    aria-describedby="alert-dialog-slide-description"
                    id='dialogContainer'>
                    <div className='NotEnoughAces'>
                        <DialogTitle>Poker Room</DialogTitle>
                        <DialogContent>
                            {dialogContent}
                        </DialogContent>
                        <DialogActions >
                            <Button onClick={handleClose} className='actionButton'>Disagree</Button>
                            <Button onClick={handleClose} className='actionButton'>Agree</Button>
                        </DialogActions>
                    </div>
                </Dialog>
            </Card>
        </StyledEngineProvider>
    )
}